exports.install = function (Vue, options) {
    Vue.prototype.encryption = function (code) {
        var c = String.fromCharCode(window.btoa(code).charCodeAt(0) + window.btoa(code).length);
        for (var i = 1; i < window.btoa(code).length; i++) {
		   		c += String.fromCharCode(window.btoa(code).charCodeAt(i) + window.btoa(code).charCodeAt(i - 1));
		 		}
		 		return escape(c);
    };
};



