
import axios from 'axios';
import qs from 'qs';

let base = 'http://test.cheeruislands.com';
let base2 = 'http://price.cheeruislands.com';
// let base = '';

// 发送验证码接口
export const accountSendCode = (params) => { return axios.post(`${base}/TestAcc/accountSendCode`, qs.stringify(params)); };
// 检测验证码是否正确
export const checkVerifyCode = (params) => { return axios.post(`${base}/TestAcc/checkVerifyCode`, qs.stringify(params)); };
// 新增or修改密码
export const updateAccountPassport = (params) => { return axios.post(`${base}/TestAcc/updateAccountPassport`, qs.stringify(params)); };
// 登录接口
export const accountLogin = (params) => { return axios.post(`${base}/TestAcc/accountLogin`, qs.stringify(params)); };
// 修改昵称
export const updateAccountUserName = (params) => { return axios.post(`${base}/TestAcc/updateAccountUserName`, qs.stringify(params)); };
// 修改手机最后一步
export const backUpdatePhone = (params) => { return axios.post(`${base}/TestAcc/backUpdatePhone`, qs.stringify(params)); };
// 个人中心修改手机
export const phoneSendCode = (params) => { return axios.post(`${base}/TestAcc/phoneSendCode`, qs.stringify(params)); };
// 后台修改密码发送验证码
export const backSendCode = (params) => { return axios.post(`${base}/TestAcc/backSendCode`, qs.stringify(params)); };
// 后台修改密码发送验证码
export const backcheckVerifyCode = (params) => { return axios.post(`${base}/TestAcc/backcheckVerifyCode`, qs.stringify(params)); };
// 后台检测验证码是否成功
export const backCheckCode = (params) => { return axios.post(`${base}/TestAcc/backCheckCode`, qs.stringify(params)); };
// 后台修改密码
export const backUpdatePassword = (params) => { return axios.post(`${base}/TestAcc/backUpdatePassword`, qs.stringify(params)); };
// 获得常用人信息列表
export const getPersonList = (params) => { return axios.post(`${base}/TestAcc/getPersonList`, qs.stringify(params)); };
// 修改常用人信息
export const updatePersonInfo = (params) => { return axios.post(`${base}/TestAcc/updatePersonInfo`, qs.stringify(params)); };
// 获得常用人信息
export const getPersonInfo = (params) => { return axios.post(`${base}/TestAcc/getPersonInfo`, qs.stringify(params)); };
// 删除常用人信息
export const deletePersonInfo = (params) => { return axios.post(`${base}/TestAcc/deletePersonInfo`, qs.stringify(params)); };
// 新增定制信息
export const addCustomerData = (params) => { return axios.post(`${base}/customer/addCustomerData`, qs.stringify(params)); };
// 获取所有案例信息
export const getAllCaseData = (params) => { return axios.post(`${base}/case/getAllCaseData`, qs.stringify(params)); };
// 获取部分案例信息
export const getPartOfCaseData = (params) => { return axios.post(`${base}/case/getPartOfCaseData`, qs.stringify(params)); };
// 获取首图图片信息
export const getCaseImageData = params => { return axios.post(`${base}/image/getCaseImageData`, qs.stringify(params)); };
// 获得用户名称
export const getUserName = params => { return axios.post(`${base}/TestAcc/getUserName`, qs.stringify(params)); };
// 通过id查看案例
export const getCaseDataByCaseId = (params) => { return axios.post(`${base}/case/getCaseDataByCaseId`, qs.stringify(params)); };
// 获取海岛分页信息
export const getPlacePage = (params) => { return axios.post(`${base}/Search/getPlacePage`, qs.stringify(params)); };
// 获得国家列表（含排序）
export const getSortCountryList = (params) => { return axios.post(`${base}/Sort/getSortCountryList`, qs.stringify(params)); };
// 搜索海岛线路信息
export const webSearchRoute = (params) => { return axios.post(`${base}/Search/webSearchRoute`, qs.stringify(params)); };
// 前端页面获取板块与线路
export const webTempRoute = (params) => { return axios.post(`${base}/Temp/webTempRoute`, qs.stringify(params)); };
// 获取所有Banner信息
export const getAllBannerInfo = (params) => { return axios.post(`${base}/Banner/getAllBannerInfo`, qs.stringify(params)); };
// 初始化获取所有线路价格
export const getRoutePlainFare = (params) => { return axios.post(`${base2}/route/getRoutePlainFare`, qs.stringify(params)); };
// 获得线路所有描述信息
export const getDescAllInfo = (params) => { return axios.post(`${base2}/routeDesc/getDescAllInfo`, qs.stringify(params)); };



