<template>
  <div id="login">
    <div class="content">
      <img src="./images/bg.png" alt="" class="bg">
      <div class="mask"></div>
      <div class="loginbox">
        <div class="loginbox_top">
          <img src="./images/logo.png" alt="" class="logo">
        </div>
        <transition name="fade">
        <div class="loginbox_bot" v-if="status === 0">
          <div class="input_item">
            <div class="input_content">
              <img src="./images/user.png" alt="" class="user">
              <input type="text" class="user_input" placeholder="使用手机号登录" v-model="login_user" maxlength="11"  v-on:keypress.enter="login">
            </div>
          </div>
          <div class="input_item">
            <div class="input_content">
              <img src="./images/password.png" alt="" class="password">
              <input type="password" class="user_input" placeholder="请输入密码" v-model="login_password" v-on:keypress.enter="login">
              <button class="forget_btn" @click="goforget">忘记密码?</button>
            </div>
          </div>
          <div class="btn_group">
            <button class="login_btn" @click="login">登录</button>
            <button class="register_btn" @click="goregister">注册</button>
          </div>
          <div class="escrow">
            <div class="escrow_title"><div class="line"></div>或使用第三方授权登陆<div class="line2"></div></div>
            <div class="escrow_btn">
              <button class="escrow_btn1" @click="gowechat">
                <img src="./images/wechat.png" alt="">
              </button>
              <button class="escrow_btn2" @click="goqq">
                <img src="./images/qq.png" alt="">
              </button>
            </div>
          </div>
        </div>
        </transition>
        <transition name="fade">
        <div class="loginbox_bot" v-if="status === 1">
          <i class="el-icon-close" @click="gologin"></i>
          <div class="input_item">
            <div class="input_content">
              <img src="./images/user.png" alt="" class="user">
              <input type="text" class="user_input" placeholder="使用手机号注册" v-model="reg_user" maxlength="11">
            </div>
          </div>
          <div class="input_item">
            <div class="input_content">
              <img src="./images/password.png" alt="" class="password">
              <input type="text" class="user_input" placeholder="请输入验证码" v-model="verify" maxlength="6">
              <input type="button" class="forget_btn" :class="{ disabledcolor: isdiabled}" @click="getVerify" value="获取验证码" ref="waitbtn">
            </div>
          </div>
          <!-- <button class="register" v-if="!okreg">立即注册</button> -->
          <button class="register reg_success" @click="nextstep">立即注册</button>
        </div>
        </transition>
        <transition name="fade">
        <div class="loginbox_bot" v-if="status === 2">
          <div class="input_item" style="margin-top:20px;">
            <div class="input_content">
              <img src="./images/password.png" alt="" class="password">
              <input type="password" class="user_input" placeholder="请输入密码" v-model="lastpassword">
            </div>
          </div>
          <div class="input_item">
            <div class="input_content">
              <img src="./images/password.png" alt="" class="password">
              <input type="password" class="user_input" placeholder="请再次输入密码" v-model="lastpassword_again">
            </div>
          </div>
          <!-- <button class="register register1">完成注册</button> -->
          <button class="register reg_success register1" @click="finishreg">完成注册</button>
        </div>
        </transition>
        <transition name="fade">
        <div class="loginbox_bot loginextra" v-if="status === 4">
          <i class="el-icon-close" @click="gologin"></i>
          <div class="forget_num">
            <input type="text" class="forget_l" maxlength="11" placeholder="请输入手机号" v-model="forget_num">
            <input type="button" class="forget_r" @click="getyzm" value="获取验证码" ref="getbtn" :class="{ forget_disabled: isdiabled2 }">
          </div>
          <!-- <p class="tips">检测到您已经绑定手机号，请通过获取验证码方式修改密码。</p> -->
        </div>
        </transition>
        <transition name="fade">
        <div class="loginbox_bot loginextra" v-if="status === 5">
          <i class="el-icon-close" @click="closeit"></i>
          <p class="forget_title">输入验证码</p>
          <input type="button" class="forget_btn forget_btn1" :class="{ disabledcolor: isdiabled2 }" value="60秒后重试" ref="getagain" @click="getyzm">
          <div class="sixinput">
            <div class="line line1"></div>
            <div class="line line2"></div>
            <div class="line line3"></div>
            <div class="line line4"></div>
            <div class="line line5"></div>
            <input type="number" class="yzm_input" v-model="forget_yzm" ref="logininput">
            <transition name="fade">
             <div class="errortips" v-if="numerror">输入验证码错误,请重新输入</div>
            </transition>
          </div>
        </div>
        </transition>
        <transition name="fade">
        <div class="loginbox_bot loginextra" v-if="status === 6">
          <input type="password" class="forget_pas" placeholder="请输入新的密码" v-model="forgetpass">
          <input type="password" class="forget_pas" placeholder="重复输入新的密码" v-model="forgetpass_again">
          <button class="confirmbutton" @click="lastconfirm">确认</button>
        </div>
        </transition>
      </div>
    </div>
  </div>
</template>
<script>
import { accountSendCode, checkVerifyCode, updateAccountPassport, accountLogin } from '../../api/api'
export default {
  data () {
    return {
      status: 0,
      login_user: '',
      login_password: '',
      reg_user: '',
      verify: '',
      isdiabled: false,
      isdiabled2: false,
      forget_num: '',
      forget_yzm: '',
      numerror: false,
      lastpassword: '',
      lastpassword_again: '',
      forgetpass: '',
      forgetpass_again: ''
    }
  },
  watch: {
    forget_yzm() {
      if (this.forget_yzm.length === 6) {
        this.$refs.logininput.blur();
        this.$refs.logininput.disabled = true;
        let info = {
          call_phone: this.forget_num,
          code_number: this.forget_yzm
        };
        checkVerifyCode(info).then((res) => {
          if (res.data !== '验证通过') {
            this.numerror = true;
            setTimeout(() => {
              this.$refs.logininput.disabled = false;
              this.numerror = false;
              this.forget_yzm = '';
              this.$refs.logininput.focus();
            }, 1800);
          } else {
            this.status = 6;
            this.$message({
            type: 'success',
            message: '验证成功'
          });
          }
        });
      }
    }
  },
  created() {
  },
  methods: {
    getCookie(name) {
      if (document.cookie.length > 0) {
        var start = document.cookie.indexOf(name + '=');
        if (start !== -1) {
        start = start + name.length + 1
        var end = document.cookie.indexOf(';', start);
        if (end === -1) end = document.cookie.length
        return unescape(document.cookie.substring(start, end))
        }
        }
        return ''
    },
    setCookie(name, value, expiredays) {
      var exdate = new Date();
      exdate.setDate(exdate.getDate() + expiredays);
      document.cookie = name + '=' + escape(value) + ((expiredays === null) ? '' : '; expires=' + exdate.toGMTString());
    },
    gowechat() {
      window.location.href = 'http://test.cheeruislands.com/TestAcc/wechatLoginView';
    },
    goqq() {
      window.location.href = 'http://test.cheeruislands.com/TestAcc/qqLoginView';
    },
    closeit() {
      this.status = 0;
    },
    lastconfirm() {
      if (this.forgetpass === '') {
        this.$message('请输入密码');
      } else if (!/^(?![A-Z]+$)(?![a-z]+$)(?!\d+$)(?![\W_]+$)\S{8,16}$/.test(this.forgetpass)) {
        this.$message({
          type: 'error',
          message: '密码必须包含字母和数字,长度8位以上'
        });
      } else if (this.forgetpass_again === '') {
        this.$message('请再次输入密码');
      } else if (this.forgetpass !== this.forgetpass_again) {
        this.$message({
          type: 'error',
          message: '两次输入密码不一致'
        });
      } else {
        let info = {
          call_phone: this.forget_num,
          type: 'register',
          password: this.forgetpass,
          request_password: this.forgetpass_again
        };
        updateAccountPassport(info).then((res) => {
          if (res.data === '修改密码成功') {
            this.$message({
              type: 'success',
              message: '修改密码成功,请重新登录'
            });
            setInterval(() => {
              window.location.href = 'login.html';
            }, 2000);
          } else {
            this.$message({
              type: 'success',
              message: '修改密码失败'
            });
          }
        });
      }
    },
    finishreg() {
      if (this.lastpassword === '') {
        this.$message('请输入密码');
      } else if (!/^(?![A-Z]+$)(?![a-z]+$)(?!\d+$)(?![\W_]+$)\S{8,16}$/.test(this.lastpassword)) {
        this.$message({
          type: 'error',
          message: '密码必须包含字母和数字,长度8位以上'
        });
      } else if (this.lastpassword_again === '') {
        this.$message('请再次输入密码');
      } else if (this.lastpassword !== this.lastpassword_again) {
        this.$message({
          type: 'error',
          message: '两次输入密码不一致'
        });
      } else {
        let info = {
          call_phone: this.reg_user,
          type: 'register',
          password: this.lastpassword,
          request_password: this.lastpassword_again
        };
        updateAccountPassport(info).then((res) => {
          if (res.data === '修改密码成功') {
            this.$message({
              type: 'success',
              message: '注册成功,请登录账号'
            });
            setInterval(() => {
              window.location.href = 'login.html';
            }, 2000);
          } else {
            this.$message({
              type: 'success',
              message: '注册失败'
            });
          }
        });
      }
    },
    login() {
      if (this.login_user === '') {
        this.$message('请输入手机号');
      } else if (this.login_password === '') {
        this.$message('请输入密码');
      } else {
        let info = {
          call_phone: this.login_user,
          account_password: this.login_password
        };
        accountLogin(info).then((res) => {
          if (res.data === '手机号码不存在') {
            this.$message({
              type: 'error',
              message: '该账号不存在'
            });
          } else if (res.data === '密码错误') {
            this.$message({
              type: 'error',
              message: '密码错误'
            });
          } else if (typeof (res.data) === 'object') {
            this.$message({
              type: 'success',
              message: '登录成功'
            });
            let tem = {
              'www': this.encryption(res.data.acc_id),
              'xxx': this.encryption(res.data.token),
              'yyy': this.encryption(res.data.call_phone),
              'zzz': res.data.user_name
            };
            sessionStorage.setItem(this.encryption('info'), JSON.stringify(tem));
            setInterval(() => {
              window.location.href = 'personalaccount.html';
            }, 1500);
          }
        });
      }
    },
    getcount() {
      if (this.$refs.getagain) {
        this.$refs.getagain.setAttribute('disabled', 'disabled');
        let times = 60;
        let timer = null;
        this.isdiabled2 = true;
        this.$refs.getagain.setAttribute('disabled', 'disabled');
        this.$refs.logininput.focus();
        timer = setInterval(() => {
          this.isdiabled2 = true;
          times = times - 1;
          if (this.$refs.getagain === undefined) {
          } else {
            this.$refs.getagain.value = times + '秒后重试';
          }
          if (this.$refs.getbtn === undefined) {
          } else {
            this.$refs.getbtn.setAttribute('disabled', 'disabled');
            this.$refs.getbtn.value = times + '秒后重试';
          }
          if (times === 0) {
            clearInterval(timer);
            if (this.$refs.getbtn !== undefined) {
              this.$refs.getbtn.removeAttribute('disabled');
              this.$refs.getbtn.value = '获取验证码';
            }
            if (this.$refs.getagain !== undefined) {
              this.$refs.getagain.value = '重新获取验证码';
              this.$refs.getagain.removeAttribute('disabled');
            }
            this.isdiabled2 = false;
          }
        }, 1000);
      }
    },
    getyzm() {
      if (this.forget_num === '') {
        this.$message('请输入手机号');
      } else if (!(/^1[34578]\d{9}$/.test(this.forget_num))) {
        this.$message('请输入正确的手机号');
      } else {
        let info = {
          call_phone: this.forget_num,
          type: 'forget_passport'
        };
        accountSendCode(info).then((res) => {
          console.log(res);
          if ((res.data === '短信已发送2，请注意查收') || (res.data === '短信已发送，请注意查收')) {
            this.$message({
              type: 'success',
              message: '获取验证码成功'
            });
            this.status = 5;
            let times = 60;
            let timer = null;
            this.isdiabled2 = true;
            this.$nextTick(() => {
              this.getcount();
            });
          } else {
            this.$message({
              type: 'error',
              message: res.data
            });
          }
        });
      }
    },
    goregister() {
      this.status = 1;
      this.$nextTick(() => {
        if (this.$refs.waitbtn) {
          if (this.isdiabled === true) {
            this.$refs.waitbtn.setAttribute('disabled', 'disabled');
          }
      }
      });
    },
    gologin() {
      this.status = 0;
      this.login_user = '';
      this.login_password = '';
    },
    nextstep() {
      if (this.reg_user === '') {
        this.$message('请输入手机号');
      } else if (this.verify === '') {
        this.$message('请输入验证码');
      } else if (!(/^1[34578]\d{9}$/.test(this.reg_user))) {
        this.$message('请输入正确的手机号');
      } else {
        let info = {
          call_phone: this.reg_user,
          code_number: this.verify
        };
        checkVerifyCode(info).then((res) => {
          if (res.data === '验证通过') {
            this.$message({
              type: 'success',
              message: '验证成功'
            });
            this.status = 2;
          } else {
            this.$message({
              type: 'error',
              message: '验证失败'
            });
          }
        });
      }
    },
    goforget() {
      this.status = 4;
    },
    getagain() {
      this.$message({
        type: 'success',
        message: '获取验证码成功'
      });
      this.getcount();
    },
    // /^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$/
    getVerify() {
      if (this.reg_user === '') {
        this.$message('请输入手机号');
      } else if (!(/^1[34578]\d{9}$/.test(this.reg_user))) {
        this.$message('请输入正确的手机号');
      } else {
        let info = {
          call_phone: this.reg_user,
          type: 'register'
        };
        accountSendCode(info).then((res) => {
          if ((res.data === '短信已发送2，请注意查收') || (res.data === '短信已发送，请注意查收')) {
              console.log(res);
              this.$message({
                type: 'success',
                message: res.data
              });
              let times = 60;
              let timer = null;
              this.isdiabled = true;
              this.$refs.waitbtn.setAttribute('disabled', 'disabled');
              timer = setInterval(() => {
                this.isdiabled = true;
                times = times - 1;
                if (this.$refs.waitbtn === undefined) {
                } else {
                  this.$refs.waitbtn.value = times + '秒后重试';
                }
                if (times === 0) {
                  clearInterval(timer);
                  if (this.$refs.waitbtn) {
                    this.$refs.waitbtn.value = '获取验证码';
                    this.$refs.waitbtn.removeAttribute('disabled');
                  }
                  this.isdiabled = false;
                }
              }, 1000);
            } else {
              this.$message({
                type: 'error',
                message: res.data
              });
            }
        });
      }
    }
  }
}
</script>
